<template>
  <el-scrollbar>
    <el-menu>

      <template v-for="menu in menuData" :key="menu.path+menu.name">
        <!--菜单项的index值，以及v-for的key值，现已暂时改为path值与name值的合体，因为path值可能会重复-->
        <el-sub-menu v-if="menu.children!=undefined&&menu.children.length>0" :index="menu.path+menu.name">
          <template #title>
            <el-icon><component v-if="menu.icon!=''" :is="menu.icon"/></el-icon>
            {{ menu.name }}
          </template>
          <template v-for="menuItem in menu.children" :key="menuItem.path+menuItem.name">
            <el-menu-item :index="menuItem.path+menuItem.name" @click="$emit('selectMenu', menu, menuItem)">
              <template #title>
                <el-icon><component v-if="menuItem.icon!=''" :is="menuItem.icon"/></el-icon>
                {{ menuItem.name }}
              </template>
            </el-menu-item>
          </template>
        </el-sub-menu>
        <el-menu-item v-else :index="menu.path+menu.name" @click="$emit('selectMenu', menu)">
          <template #title>
            <el-icon><component v-if="menu.icon!=''" :is="menu.icon"/></el-icon>
            {{ menu.name }}
          </template>
        </el-menu-item>
      </template>

    </el-menu>
  </el-scrollbar>
</template>

<script>
export default {
  name: "Menu",
  props: ['menuData'],
}
</script>

<style scoped>

</style>
